<template>
    <div class="login_page">

        <div class="container">
            <form @submit.prevent="Change_password()" class="form_login" v-if="!no_validate_tokint">
                <md-layout md-gutter>
                    <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100" md-flex-large="100"
                               md-flex-xlarge="100" class="pad">
                        <md-input-container v-bind:class="[!required_pass ? activeClass : 'md-input-invalid']"
                                            md-has-password>
                            <md-icon>lock</md-icon>

                            <label>Password</label>
                            <md-input type="password" v-model="password"></md-input>
                            <span class="md-error">required</span>

                        </md-input-container>

                    </md-layout>
                    <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100" md-flex-large="100"
                               md-flex-xlarge="100" class="pad">
                        <md-input-container
                                v-bind:class="[!validate_confirm_password ? activeClass : 'md-input-invalid']"
                                md-has-password>
                            <md-icon>lock</md-icon>

                            <label>Confirm password</label>
                            <md-input type="password" v-model="confirm_password"></md-input>
                            <span class="md-error">Passwords must match</span>

                        </md-input-container>
                        <span class="md-error" style="color: red" v-if="error_login">{{error_login}}</span>

                    </md-layout>

                </md-layout>

                <md-button type="submit" class="md-raised md-accent"
                           :disabled="validate_confirm_password && !required_pass || !token"> send
                </md-button>

            </form>
            <div v-else class="form_login" style="text-align: center;border: 1px solid red;border-radius: 5px">
                Your token is expired, try again or contact administrator to change your credentials.

            </div>
        </div>

    </div>
</template>

<script>
    import router from '.././router'

    export default {
        components: {},
        name: 'Change-password',
        data() {
            return {
                validate_confirm_password: false,
                required_field: false,
                required_pass: false,
                no_validate_tokint: false,
                password_form: {},
                token: '',
                login: '',
                error_login: '',
                password: '',
                confirm_password: '',
                activeClass: '',
            }
        },
        router,
        methods: {
            Change_password() {
                if (this.password === '') {
                    this.required_pass = true;
                    return
                }
                if (this.confirm_password === '') {
                    this.validate_confirm_password = true;
                    return
                }
                this.$parent.$refs.topProgress.start()
                this.password_form.password = this.password
                this.password_form.confirm_password = this.confirm_password
                this.$http.put(this.$parent.base_url + 'change_password', this.password_form,
                    {headers: {'Authorization': 'Bearer ' + this.token}}).then(
                    function (response) {
                        this.$parent.log_out();
                        this.nav_bar = false;
                        this.admin = false;
                        this.error = false;
                        this.$cookies.set("token", '');
                        this.$cookies.set("admin", '');
                        router.push('/login');

                        this.$parent.$refs.topProgress.done()
                    }, function (error) {
                        console.log(error)
                        this.$parent.$refs.topProgress.fail()
                    }
                );
                self = this
                setTimeout(function () {
                    self.$parent.success = false;
                    self.$parent.error = false;
                }, 1000);
            },
        }, watch: {

            confirm_password: function () {
                this.error_login = ''

                if (this.confirm_password) {
                    this.validate_confirm_password = false;
                    if (this.confirm_password != this.password) {
                        this.validate_confirm_password = true;
                    }

                } else {
                    this.validate_confirm_password = false
                }

            }, password: function () {
                this.error_login = ''

                if (this.password) {
                    if (this.confirm_password) {
                        this.validate_confirm_password = false;
                        if (this.confirm_password != this.password) {
                            this.validate_confirm_password = true;
                        }

                    } else {

                        this.validate_confirm_password = false
                    }
                    this.required_pass = false

                } else {
                    this.required_pass = true

                }

            },
        }, created() {
            this.$parent.interval=false
            if (this.$route.query.token == undefined) {

                this.token = this.$cookies.get("token");
                if (!this.$cookies.get("token")) {
                    router.push('/login');

                }

            } else {
                    this.$parent.$refs.topProgress.start()
                    this.$http.get(this.$parent.base_url + 'check_token',
                        {headers: {'Authorization': 'Bearer ' + this.$route.query.token}}).then(
                        function (response) {
                            if(response.body.result){
                                this.$cookies.set("token",'');
                                this.$cookies.remove("token");
                                this.$cookies.remove("admin");
                                this.$parent.nav_bar = false;
                                this.$parent.admin = false;
                                this.$parent.error = false;
                                this.token=this.$route.query.token
                            }
                            this.no_validate_tokint = !response.body.result
                            this.$parent.$refs.topProgress.done()

                        }, function (error) {
                            console.log(error.status)
                            this.no_validate_tokint = true
                            this.$parent.$refs.topProgress.fail()

                        }
                    );
                }
            this.$parent.title = 'Change password'
        }


    }

</script>
<style>
</style>



