<template>
    <div>
        <md-table>
            <md-table-header>
                <md-table-row>
                    <md-table-head>Email</md-table-head>
                    <md-table-head>Name</md-table-head>
                    <md-table-head>Clients</md-table-head>
                    <md-table-head>Phone</md-table-head>
                    <md-table-head>Role</md-table-head>
                    <md-table-head v-if="$parent.admin" md-numeric></md-table-head>
                    <md-table-head v-if="$parent.admin" md-numeric></md-table-head>
                </md-table-row>
            </md-table-header>


            <md-table-body>
                <md-table-row v-for="(item, index) in users" :key="index">
                    <md-table-cell>{{item.login}}</md-table-cell>
                    <md-table-cell style="text-transform: capitalize">{{item.name}}
                        <span v-if="!item.name">—</span>
                    </md-table-cell>
                    <md-table-cell style="max-width: 250px">
                        <span v-for="(it,i) in item.clients">
                            {{it.name}}<span v-if="i != item.clients.length - 1">, </span></span>
                        <span v-if="!item.clients[0]">—</span>
                    </md-table-cell>
                    <md-table-cell>{{item.phone}}
                        <span v-if="!item.phone">—</span>
                    </md-table-cell>
                    <md-table-cell style="text-transform: capitalize">{{item.role}}</md-table-cell>

                    <md-table-cell v-if="$parent.admin" >
                        <md-button @click="EditUser(item.user_id ,index)">
                            <md-icon>edit</md-icon>
                        </md-button>
                    </md-table-cell>
                    <md-table-cell v-if="$parent.admin" >
                        <md-button @click="DeleteUsers(item.user_id ,index)">
                            <md-icon>delete</md-icon>
                        </md-button>
                    </md-table-cell>
                </md-table-row>
            </md-table-body>
        </md-table>
        <!--<transition name="fade">-->
            <!--<main-fonts></main-fonts>-->
        <!--</transition>-->

    </div>

</template>

<script>
    import router from '.././router'
    import MainFonts from './fonts.vue'

    export default {

//        components: {MainFonts},
        name: 'users',

        data() {
            return {
                users: [],
                current_index: null
            }
        },
        router,
        methods: {
            getUsers() {
                this.$parent.$refs.topProgress.start()

                this.$http.get(this.$parent.base_url + 'users',
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.users = response.body;
                        this.$parent.$refs.topProgress.done()

                    }, function (error) {
                        console.log(error.status)
                        this.$parent.error = true;
                        this.$parent.$refs.topProgress.fail()
                        this.$parent.UNAUTHORIZED(error.status)

                    }
                );
            },

            EditUser(id, index) {
                this.$parent.modalStations = false

                this.$parent.modalUsers = true
                this.current_index = index;

                this.$parent.words = {
                    'captions': 'Edit user',
                    update: true,
                    create: false,
                    obj: this.users[index],
                    id: id

                }
            },
            DeleteUsers(id, index) {
                this.current_index = index;

                this.$parent.words_delete = {
                    'captions': 'users',
                    station: false,
                    user: true,
                    obj: {
                        current_index: index,
                        id: id
                    }

                }
                this.$parent.modalDelete = true

            },

        },
        created() {

            this.$parent.interval=false

            this.$parent.title = 'Tetra users'
            this.getUsers();
        },
        computed: {
            event() {
                return this.$parent.event;
            },

        },
        watch: {
            event: function () {

                if (this.event === 'delete_user') {
                    if (this.current_index !== null) {
                        this.users.splice(this.current_index, 1);
                    }
                    this.$parent.event = '';
                }
                ;
                if (this.event === 'create_user') {
                    this.getUsers();

                    this.$parent.event = '';

                }
                ;
                if (this.event === 'update_user') {
                    this.getUsers();
                    this.$parent.event = '';
                }
            },
        }

    }

</script>

<style scoped>
    .md-table .md-table-head-text {
        padding-right: 0 !important;
        padding-left: 0 !important;

    }

    .md-table {
        margin-top: 5% !important;
    }

    .md-table .md-table-head {
        padding-left: 0 !important;

        text-align: center !important;
    }


</style>



