<template>
    <div>
        <div tabindex="0" class="md-dialog-container md-theme-default md-active">
            <div class="md-dialog md-reference">
                <div class="md-dialog-title md-title">Descriptions of alarms
                </div>
                <div class="md-dialog-content">
                    <div class="alarm-info">
                        <div class=" round  green help"></div>
                        <div class="alarm-desc">OK, system function is normal</div>
                    </div>
                    <div  class="alarm-info">
                        <div class=" round  yellow help"></div>
                        <div class="alarm-desc">Warning, system function is not likely to be affected</div>
                    </div>
                    <div  class="alarm-info">
                        <div class=" round  purple help"></div>
                        <div class="alarm-desc">Alarm, system function is partly affected</div>
                    </div>
                    <div  class="alarm-info">
                        <div class=" round  red help"></div>
                        <div class="alarm-desc">Blocked , some components of the system are not active </div>
                    </div>
                    <div  class="alarm-info">
                        <div class=" round  grey help"></div>
                        <div class="alarm-desc">IP contact lost, might affect system availability (only applicable for multisite networks ) </div>
                    </div>
                </div>

                <div class="md-dialog-actions">
                    <md-button class="md-primary" @click="closeDialog()">Got It!</md-button>

                </div>
            </div>
            <div class="md-backdrop md-dialog-backdrop md-active"></div>

        </div>
    </div>
</template>

<script>
    import router from '.././router'

    export default {
        components: {},
        name: 'help',
        data() {
            return {

            }
        },
        router,
        methods: {
            closeDialog() {
                this.$parent.modalHelp = false
            },

        }, created() {

        }


    }

</script>

<style scoped>
    .help {
        float: left;
        margin-right: 25px;
    }
    .alarm-info{
        margin-bottom: 15px;
    }
.alarm-desc{
    margin-left: 45px;
}
    .md-dialog {
        margin-bottom: 15vh !important;
        /*width: unset !important;*/
        /*height: unset !important;*/
    }

    .md-dialog {
        /*width: 80%;*/
        min-width: 300px !important;
        /*max-width: unset !important;*/
    }

    .notification-passive {
        color: #0000008a;
    }

    .notification-passive {
        color: #0000008a;
    }

    .notification-active {
        color: #3F51B9;

    }

    .notification {
        cursor: pointer;
    }

    .flex-container {
        display: flex;
        align-items: center;
    }

    .flex-container > div {
        /*background-color: #f1f1f1;*/
        width: 100px;
        margin: 10px;
        text-align: center;
        /*line-height: 75px;*/
        /*font-size: 30px;*/
    }

    .custom-select {
        margin: 0;
        margin-left: 5px;
        line-height: unset;
        float: left;
    }

    .button {
        position: relative;
        display: inline-block;
        cursor: pointer;

        -webkit-transition: all 0.4s ease-in;
        transition: all 0.4s ease-in;
    }

    .button:before {
        content: '';
        background-color: #3F51B9;
        border-radius: 50%;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        -webkit-transform: scale(0.001, 0.001);
        transform: scale(0.001, 0.001);
    }

    .button:focus {
        outline: 0;
    }

    .button:focus:before {
        -webkit-animation: effect_dylan 0.8s ease-out;
        animation: effect_dylan 0.8s ease-out;
    }

    @-webkit-keyframes effect_dylan {
        50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
        }
        99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
        }
        100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
        }
    }

    @keyframes effect_dylan {
        50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
        }
        99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
        }
        100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
        }
    }


</style>



