<template>
    <div>
        <div tabindex="0" class="md-dialog-container md-theme-default md-active">
            <div class="md-dialog md-reference">
                <div class="md-dialog-title md-title">Logs
                </div>
                <div class="md-dialog-content" v-if="!logs[0]">
                    Logs are empty
                </div>
                <div class="md-dialog-content" v-else>
                        <div class="flex-container">
                            <div style="width: 100%; font-weight: 500">Date</div>

                            <div style="width: 100%; font-weight: 500">Log</div>
                        </div>
                        <div class="flex-container" v-for="item in logs ">
                            <div style="width: 100%;">
                                {{item.time}}
                            </div>
                            <div style="width: 100%;">
                                {{item.text}}

                            </div>
                        </div>
                </div>
                <div class="md-dialog-actions">
                    <md-button class="md-primary" @click="closeDialog()">Cancel</md-button>

                </div>
            </div>
            <div class="md-backdrop md-dialog-backdrop md-active"></div>

        </div>
    </div>
</template>

<script>
    import router from '.././router'

    export default {
        components: {},
        name: 'logs',
        data() {
            return {
                validate_confirm_password: false,
                required_field: false,
                required_pass: false,
                no_validate_tokint: false,
                isAdmin: false,
                password_form: {},
                logs: {},
                token: '',
                login: '',
                error_login: '',
                password: '',
                confirm_password: '',
                activeClass: '',
            }
        },
        router,
        methods: {
            closeDialog() {
                this.$parent.modalLogs = false
                router.push('/');

            },
            doneNotifacation() {
            },
            getLogs() {
                this.$parent.$refs.topProgress.start()

                this.$http.get(this.$parent.base_url + 'station/logs/' + this.$parent.logs_id,
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.logs = response.body.result;
                        this.$parent.$refs.topProgress.done()
                        this.$parent.logs_id = null;

                    }, function (error) {
                        console.log(error.status)
                        this.$parent.error = true;
                        this.$parent.logs_id = null;

                        this.$parent.$refs.topProgress.fail()
                        this.$parent.UNAUTHORIZED(error.status)

                    }
                );
            },
        }, created() {
            this.getLogs()
            if (this.$route.query.token == undefined) {

                this.token = this.$cookies.get("token");
                if (!this.$cookies.get("token")) {
                    router.push('/login');
                }

            } else {
                this.$parent.$refs.topProgress.start()

                this.$http.get(this.$parent.base_url + 'check_token',
                    {headers: {'Authorization': 'Bearer ' + this.$route.query.token}}).then(
                    function (response) {
                        if (response.body.result) {
                            this.$cookies.set("token", '');

                            this.$cookies.remove("token");
                            this.$cookies.remove("admin");

                            this.$parent.nav_bar = false;
                            this.$parent.admin = false;
                            this.$parent.error = false;
                            this.token = this.$route.query.token
                        }
                        this.no_validate_tokint = !response.body.result
                        this.$parent.$refs.topProgress.done()

                    }, function (error) {
                        console.log(error.status)
                        this.no_validate_tokint = true
                        this.$parent.$refs.topProgress.fail()

                    }
                );
            }
        }


    }

</script>

<style scoped>
    .md-dialog {
        /*margin-top: 10vh !important;*/
        /*width: unset !important;*/
        /*height: unset !important;*/
    }

    .md-dialog {
        width: 80%;
        min-width: 300px !important;
        max-width: unset !important;
    }

    .notification-passive {
        color: #0000008a;
    }

    .notification-passive {
        color: #0000008a;
    }

    .notification-active {
        color: #3F51B9;

    }

    .notification {
        cursor: pointer;
    }

    .flex-container {
        display: flex;
        align-items: center;
    }

    .flex-container > div {
        /*background-color: #f1f1f1;*/
        width: 100px;
        margin: 10px;
        text-align: center;
        /*line-height: 75px;*/
        /*font-size: 30px;*/
    }

    .custom-select {
        margin: 0;
        margin-left: 5px;
        line-height: unset;
        float: left;
    }

    .button {
        position: relative;
        display: inline-block;
        cursor: pointer;

        -webkit-transition: all 0.4s ease-in;
        transition: all 0.4s ease-in;
    }

    .button:before {
        content: '';
        background-color: #3F51B9;
        border-radius: 50%;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        -webkit-transform: scale(0.001, 0.001);
        transform: scale(0.001, 0.001);
    }

    .button:focus {
        outline: 0;
    }

    .button:focus:before {
        -webkit-animation: effect_dylan 0.8s ease-out;
        animation: effect_dylan 0.8s ease-out;
    }

    @-webkit-keyframes effect_dylan {
        50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
        }
        99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
        }
        100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
        }
    }

    @keyframes effect_dylan {
        50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
        }
        99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
        }
        100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
        }
    }


</style>



