<template>
    <div>
        <md-whiteframe md-elevation="1" class="main-toolbar">
            <md-toolbar>
                <div class="md-toolbar-container  container">
                    <div class="md-icon-button">
                        <router-link to="/stations" class="nav-item">

                            <img src="../assets/logo.svg" class="logo-image">
                        </router-link>
                    </div>
                    <div style=" display: flex; flex-wrap: wrap; align-items: center;  ">
                        <div class=" round status-header" :class="status"></div>
                        <div class="md-title title">
                            OVERALL STATUS
                        </div>
                    </div>
                    <router-link to="/" class="nav-item">
                        <md-button class="nav-item-text"  @click="$parent.openHelp();">

                            <span class="header-text">
                                                             <md-icon> help_outline</md-icon>



                                <span>  help</span>
              </span>
                        </md-button>
                    </router-link>
                    <span style="flex: 1"></span>
                    <router-link to="/stations" class="nav-item">
                        <md-button class="nav-item-text">

                            <span class="header-text">
                             <md-icon> wifi</md-icon>
                                <span>  NETWORKS</span>
              </span>
                        </md-button>
                    </router-link>

                    <router-link to="/users" class="nav-item" v-if="$parent.admin">
                        <md-button class="nav-item-text">
                            <span class="header-text">
                                                            <md-icon>people</md-icon>

                                <span> Users</span>
                              </span>
                        </md-button>
                    </router-link>

                    <router-link to="" class="nav-item">

                        <!--<md-menu md-align-trigger ref="menu">-->
                        <md-menu :md-offset-x="194" md-offset-y="72" ref="menu">
                            <md-button md-menu-trigger>
                                <md-avatar class="avatar">

                                    <img src="../assets/avatar.png" alt="People">
                                </md-avatar>
                            </md-button>

                            <md-menu-content>

                                <md-menu-item>
                                    <router-link to="/password/change" class="nav-item">

                                        <md-button class="btn-menu">

                                            <md-icon>enhanced_encryption</md-icon> change password
                                        </md-button>
                                    </router-link>
                                </md-menu-item>
                                <md-menu-item>
                                    <router-link to="/" class="nav-item " style="color: black">

                                        <md-button class="btn-menu" @click="$parent.openNotifications();$refs.menu.close()">

                                            <md-icon>notifications</md-icon>    notifications
                                        </md-button>
                                    </router-link>
                                </md-menu-item>
                                <md-menu-item v-if="$parent.admin">
                                    <router-link to="/" class="nav-item " style="color: black">

                                        <md-button class="btn-menu" @click="$parent.openSettings();$refs.menu.close()">

                                            <md-icon>settings</md-icon>  global  settings
                                        </md-button>
                                    </router-link>
                                </md-menu-item>
                                <md-menu-item>
                                    <router-link to="/login" class="nav-item" style="color: black">

                                        <md-button class="nav-item btn-menu" @click="$parent.log_out()">
                                            <md-icon>power_settings_new</md-icon> logout
                                        </md-button>
                                    </router-link>
                                </md-menu-item>
                            </md-menu-content>
                        </md-menu>
                    </router-link>

                </div>


            </md-toolbar>
        </md-whiteframe>

    </div>
</template>

<script>
    import router from '.././router'

    export default {

        name: 'header-main',

        data() {
            return {
                status: 'green',
                clearInterval: '',
                progress: 2,
                progressInterval: null,
                done: false,
                transition: true
            }
        },
        router,
        methods: {
            getStatus() {
                this.$parent.$refs.topProgress.start()
                this.$http.get(this.$parent.base_url + 'status',
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.status = response.body.result.status;
                        if (this.status === -1) {
                            this.status = 'grey';
                        } else if (this.status === 0) {
                            this.status = 'green';
                        }
                        else if (this.status === 1) {
                            this.status = 'yellow';
                        } else if (this.status === 2) {
                            this.status = 'purple';
                        } else if (this.status === 3) {
                            this.status = 'red';
                        } else {
                            this.status = 'grey';
                        }
                        clearInterval(this.clearInterval)

                        this.clearInterval = window.setTimeout(() => {
                            this.getStatus()
                        } ,1000 * 30)
                        this.$parent.$refs.topProgress.done()

                    }, function (error) {
                        console.log(error.status)
                        this.$parent.error = true;
                        this.$parent.$refs.topProgress.fail()
                        this.$parent.UNAUTHORIZED(error.status)

                    }
                );
            },
        }, created() {
            if (!!this.$parent.admin) {
                this.getStatus()
            }
            this.getStatus()
        }


    }

</script>

<style scoped>
    .md-warn{
        color: #fff !important;
        stroke: #fff !important;

    }

    .md-list-item .md-list-item-container {
        min-height: 0 !important;
    }

    .logo-image {
        width: 170px;
        height: 46px;
    }

    .md-button {
        min-width: unset !important;
        padding: 0 5px !important;
    }

    .header-text {
        display: block;

    }

    .status-header {
        margin-left: 25px;
        width: 40px;
        height: 40px;
        border: 2px solid #fff
    }

    .title {
        display: block;

        margin-left: 5px !important;
        color: #fff;
        font-size: 14px !important;
        font-weight: 500 !important;
    }

    @media screen and (max-width: 766px) {

        .title {
            display: none;

        }

        .status-header {
            margin-left: 25px;

            width: 20px;
            height: 20px;
        }

    }

    @media screen and (max-width: 466px) {

        .header-text span {
            display: none;

        }

    }


</style>



