<template>
    <div>
        <div tabindex="0" class="md-dialog-container md-theme-default md-active">
            <div class="md-dialog md-reference" >
                <div class="md-dialog-title md-title"> Global settings</div>
                <div class="md-dialog-content">
                    <md-layout md-gutter>
                        <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100" class="pad">
                            <md-input-container
                                    v-bind:class="[!class_for_first_alarm_timer? activeClass : 'md-input-invalid']">
                                <label>L1 alarm timer(min)</label>
                                <md-input v-model="first_alarm_timer" type="number" required></md-input>
                                <span class="md-error">Please enter valid integer number from 1 to 1440.</span>

                            </md-input-container>
                        </md-layout>
                    </md-layout>
                    <md-layout md-gutter>
                        <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100" class="pad">
                            <md-input-container
                                    v-bind:class="[!class_for_second_alarm_timer ? activeClass : 'md-input-invalid']">
                                <label>L2 alarm timer(min)</label>
                                <md-input v-model="second_alarm_timer" type="number" required></md-input>
                                <span class="md-error">Please enter valid integer number from 1 to 1440.</span>

                            </md-input-container>
                        </md-layout>
                    </md-layout>
                    <md-layout md-gutter>
                        <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100" class="pad">
                            <md-input-container
                                    v-bind:class="[!class_for_third_alarm_timer ? activeClass : 'md-input-invalid']">
                                <label>L3 alarm timer(min)</label>
                                <md-input v-model="third_alarm_timer" type="number" required></md-input>
                                <span class="md-error">Please enter valid integer number from 1 to 1440.</span>

                            </md-input-container>
                        </md-layout>
                    </md-layout>
                    <md-layout md-gutter>
                        <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100" class="pad">
                            <md-input-container
                                    v-bind:class="[!class_for_zero_alarm_timer ? activeClass : 'md-input-invalid']">
                                <label>IP loss timer(min)</label>
                                <md-input v-model="zero_alarm_timer" type="number" required></md-input>
                                <span class="md-error">Please enter valid integer number from 1 to 1440.</span>

                            </md-input-container>
                        </md-layout>
                    </md-layout>

                </div>
                <div class="md-dialog-actions">
                    <md-button class="md-primary" @click="closeDialog()">Cancel</md-button>
                    <md-button class="md-primary" @click="putSettings()"
                               :disabled="!(!class_for_first_alarm_timer && !class_for_second_alarm_timer && !class_for_third_alarm_timer && !class_for_zero_alarm_timer)">
                        Done
                    </md-button>

                </div>
            </div>
            <div class="md-backdrop md-dialog-backdrop md-active"></div>

        </div>
    </div>
</template>

<script>
    import router from '.././router'

    export default {
        components: {},
        name: 'settings',
        data() {
            return {
                validate_confirm_password: false,
                required_field: false,
                required_pass: false,
                no_validate_tokint: false,
                isAdmin: false,

                first_alarm_timer: null,
                second_alarm_timer: null,
                third_alarm_timer: null,
                zero_alarm_timer: null,

                class_for_first_alarm_timer: '',
                class_for_second_alarm_timer: '',
                class_for_third_alarm_timer: '',
                class_for_zero_alarm_timer: '',

                settings: {},
                activeClass: '',
            }
        },
        router,
        methods: {
            closeDialog() {
                this.$parent.modalSettings = false
                router.push('/');

            },

            putSettings() {
                if (this.first_alarm_timer === '') {
                    this.class_for_first_alarm_timer = true;
                    return
                }
                if (this.second_alarm_timer === '') {
                    this.class_for_second_alarm_timer = true;
                    return
                }
                if (this.third_alarm_timer === '') {
                    this.class_for_third_alarm_timer = true;
                    return
                }
                if (this.zero_alarm_timer === '') {
                    this.class_for_zero_alarm_timer = true;
                    return
                }
//
                this.$parent.$refs.topProgress.start()

                this.settings.first_alarm_timer = this.first_alarm_timer * 60
                this.settings.second_alarm_timer = this.second_alarm_timer * 60
                this.settings.third_alarm_timer = this.third_alarm_timer * 60
                this.settings.zero_alarm_timer = this.zero_alarm_timer * 60
                this.$http.put(this.$parent.base_url + 'settings', this.settings,
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {

                        this.$parent.$refs.topProgress.done()
                    }, function (error) {
                        console.log(error)
                        this.$parent.$refs.topProgress.fail()
                    }
                );
                self = this
                setTimeout(function () {
                    self.$parent.success = false;
                    self.$parent.error = false;
                }, 1000);
            },
            getSettings() {

                this.$http.get(this.$parent.base_url + 'settings',
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.first_alarm_timer = response.body.result.first_alarm_timer / 60;
                        this.second_alarm_timer = response.body.result.second_alarm_timer / 60;
                        this.third_alarm_timer = response.body.result.third_alarm_timer / 60;
                        this.zero_alarm_timer = response.body.result.zero_alarm_timer / 60;

                        this.$parent.$refs.topProgress.done()
                    }, function (error) {
                        console.log(error)
                        this.$parent.$refs.topProgress.fail()
                    }
                );
                self = this
                setTimeout(function () {
                    self.$parent.success = false;
                    self.$parent.error = false;
                }, 1000);
            },
        }, watch: {
            first_alarm_timer: function () {

                this.class_for_first_alarm_timer = this.first_alarm_timer == 1
                let number = parseInt(this.first_alarm_timer, 10)
                if (isNaN(number) || this.first_alarm_timer < 1 || this.first_alarm_timer > 1440) {
                    this.class_for_first_alarm_timer = true
                    return

                }
                this.class_for_first_alarm_timer = false
            },
            second_alarm_timer: function () {

                this.class_for_second_alarm_timer = this.second_alarm_timer == 1
                let number = parseInt(this.second_alarm_timer, 10)
                if (isNaN(number) || this.second_alarm_timer < 1 || this.second_alarm_timer > 1440) {
                    this.class_for_second_alarm_timer = true
                    return

                }
                this.class_for_second_alarm_timer = false
            },
            third_alarm_timer: function () {

                this.class_for_third_alarm_timer = this.third_alarm_timer == 1
                let number = parseInt(this.third_alarm_timer, 10)
                if (isNaN(number) || this.third_alarm_timer < 1 || this.third_alarm_timer > 1440) {
                    this.class_for_third_alarm_timer = true
                    return

                }
                this.class_for_third_alarm_timer = false
            },
            zero_alarm_timer: function () {

                this.class_for_zero_alarm_timer = this.zero_alarm_timer == 1
                let number = parseInt(this.zero_alarm_timer, 10)
                if (isNaN(number) || this.zero_alarm_timer < 1 || this.zero_alarm_timer > 1440) {
                    this.class_for_zero_alarm_timer = true
                    return

                }
                this.class_for_zero_alarm_timer = false
            },

        }, created() {
            this.getSettings()
        }


    }

</script>

<style scoped>
    .md-select {
        min-width: unset !important;
    }

    .md-dialog {
        margin-bottom: 10vh !important;

    }

    .notification-passive {
        color: #0000008a;
    }

    .notification-passive {
        color: #0000008a;
    }

    .notification-active {
        color: #3F51B9;

    }

    .notification {
        cursor: pointer;
    }

    .flex-container {
        display: flex;
        align-items: center;
    }

    .flex-container > div {
        /*background-color: #f1f1f1;*/
        width: 100px;
        margin: 10px;
        text-align: center;
        /*line-height: 75px;*/
        /*font-size: 30px;*/
    }

    .custom-select {
        margin: 0;
        margin-left: 5px;
        line-height: unset;
        float: left;
    }

    .button {
        position: relative;
        display: inline-block;
        cursor: pointer;

        -webkit-transition: all 0.4s ease-in;
        transition: all 0.4s ease-in;
    }

    .button:before {
        content: '';
        background-color: #3F51B9;
        border-radius: 50%;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        -webkit-transform: scale(0.001, 0.001);
        transform: scale(0.001, 0.001);
    }

    .button:focus {
        outline: 0;
    }

    .button:focus:before {
        -webkit-animation: effect_dylan 0.8s ease-out;
        animation: effect_dylan 0.8s ease-out;
    }

    @-webkit-keyframes effect_dylan {
        50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
        }
        99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
        }
        100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
        }
    }

    @keyframes effect_dylan {
        50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
        }
        99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
        }
        100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
        }
    }


</style>



