<template>
    <div v-on:keyup.esc="closeModals()">
        <vue-topprogress ref="topProgress" ></vue-topprogress>

        <transition v-if="nav_bar" name="fade">
            <header-main></header-main>
        </transition>
        <router-view class="container" v-title="title"></router-view>
        <md-chip v-show="success" class="success-confirm">
            <md-icon>check</md-icon>
            <span class="info-text">  success </span>
        </md-chip>
        <md-chip v-show="error" class="error-confirm">
            <md-icon>error</md-icon>
            <span class="info-text">  error </span>
        </md-chip>
        <md-speed-dial md-open="hover" class=" floaty" md-theme="purple" v-if="nav_bar && admin">
            <md-button class="md-fab" md-fab-trigger @click="CreateStations()" style="background: #1452ff;">
                <md-icon md-icon-morph>wifi</md-icon>
                <md-icon>add</md-icon>
            </md-button>
            <md-button class="md-fab md-primary md-mini md-clean floaty-list-item " style="background: rebeccapurple;"
                       @click="CreateUsers()">
                <md-icon>people</md-icon>
            </md-button>
            <md-button class="md-fab md-primary md-mini md-clean" style="background: forestgreen;"
                       @click="CreateClients()">
                <md-icon>business</md-icon>
            </md-button>
        </md-speed-dial>
        <transition v-if="modalStations" name="fade">
            <popup-stations></popup-stations>
        </transition>
        <transition v-if="modalUsers" name="fade">
            <popup-users></popup-users>
        </transition>
        <transition v-if="modalDelete" name="fade">
            <popup-delete></popup-delete>
        </transition>
        <transition v-if="modalClients" name="fade">
            <popup-clients></popup-clients>
        </transition>
        <transition v-if="modalNotifications" name="fade">
            <notifications></notifications>
        </transition>
        <transition v-if="modalLogs" name="fade">
            <logs></logs>
        </transition>
        <transition v-if="modalSettings" name="fade">
            <settings></settings>
        </transition>
        <transition v-if="modalHelp" name="fade">
            <help></help>
        </transition>
    </div>
</template>

<script>


    import router from './router'
    import HeaderMain from "./components/header.vue";
    import PopupStations from "./components/popup-stations.vue";
    import PopupUsers from "./components/popup-users.vue";
    import PopupDelete from "./components/popup-delete.vue";
    import PopupClients from "./components/popup-clients.vue";
    import Notifications from "./components/notifications.vue";
    import Settings from "./components/settings.vue";
    import Logs from "./components/logs.vue";
    import Help from "./components/help.vue";

    export default {
        components: {
            PopupClients,
            HeaderMain, PopupStations, PopupUsers, PopupDelete, Notifications, Logs, Settings,Help
        },

        name: 'app',
        router,
        data() {
            return {
                title: 'Tetra',

//                base_url: 'http://damm.wireless.no:80/',
                base_url: process.env.NODE_ENV === 'production' ? '' : 'http://damm.wireless.no:80/',
                admin: false,
                interval: false,
                modalStations: false,
                reloadClients: false,
                nav_bar: false,
                modalUsers: false,
                modalDelete: false,
                modalClients: false,
                modalSettings: false,
                modalLogs: false,
                modalHelp: false,
                modalNotifications: false,
                showNotifications: false,
                success: false,
                error: false,
                words: {},
                words_delete: {},
                event: '',
                logs_id: null,
                currentUser: {},
                currentStation: {},

            }
        },
        mounted() {

            setTimeout(() => {
                this.$refs.topProgress.done()
            }, 2000)
        },
        methods: {
            closeModals() {
                this.modalUsers = false
                this.modalDelete = false
                this.modalClients = false
                this.modalNotifications = false
                this.modalSettings = false
                this.modalLogs = false

                this.modalStations = false
            }
            ,
            UNAUTHORIZED(status) {
                if (status == 401) {
                    this.log_out()
                }
            },
            log_out() {
                console.log('logout')
                this.$cookies.set("token", '');
                this.modalNotifications = false
                this.modalSettings = false

                this.$cookies.remove("token");
                this.$cookies.remove("admin");

                this.nav_bar = false;
                this.admin = false;
                this.error = false;
                router.push('/login');
            },
            CreateStations() {
                this.modalUsers = false
                this.modalDelete = false
                this.modalClients = false
                this.modalNotifications = false
                this.modalSettings = false
                this.modalLogs = false

                this.modalStations = true
                router.push('/stations');


                this.words = {
                    'captions': 'Add node',
                    create_btn: true,
                    updata_btn: false
                }
            },
            CreateUsers() {
                this.modalStations = false
                this.modalDelete = false
                this.modalClients = false
                this.modalNotifications = false
                this.modalLogs = false
                this.modalSettings = false

                this.modalUsers = true
                router.push('/users');

                this.words = {
                    'captions': 'Add user',
                    create: true,
                    updata: false,

                }
            },
            openLogs(id) {
                this.modalStations = false
                this.modalDelete = false
                this.modalClients = false
                this.modalNotifications = false
                this.modalLogs = true
                this.modalSettings = false
                this.logs_id = id
                this.modalUsers = false

            }, openHelp() {
                this.modalStations = false
                this.modalDelete = false
                this.modalClients = false
                this.modalNotifications = false
                this.modalLogs = false
                this.modalSettings = false
                this.modalUsers = false
                this.modalHelp = true

            },
            openSettings() {
                this.modalStations = false
                this.modalDelete = false
                this.modalClients = false
                this.modalNotifications = false
                this.modalLogs = false
                this.modalSettings = true

                this.modalUsers = false

            },
            openNotifications() {
                this.modalStations = false
                this.modalDelete = false
                this.modalLogs = false
                this.modalNotifications = true
                this.modalSettings = false

                this.modalUsers = false
                this.modalClients = false
//                router.push('/users');

                this.words = {
                    'captions': 'Create network',
                    create: true,
                    updata: false,

                }
            },
            CreateClients() {
                this.modalStations = false
                this.modalDelete = false
                this.modalLogs = false
                this.modalNotifications = false
                this.modalSettings = false

                this.modalUsers = false
                this.modalClients = true
//                router.push('/users');

                this.words = {
                    'captions': 'Create network',
                    create: true,
                    updata: false,

                }
            },


        },
        created() {
            this.nav_bar = !!this.$cookies.get('token')
            this.admin = !!this.$cookies.get('admin')

        }


    }
</script>

<style>
    .md-fab {
        color: white !important;
    }

    .success-confirm {
        position: absolute;
        background: rgba(25, 168, 0, 0.6) !important;
        border-color: rgba(25, 168, 0, 1) !important;
        width: 150px;
        text-align: center;
        color: white;
        top: 70px;
        right: 5%;
    }

    .error-confirm {
        position: absolute;
        background: rgba(255, 87, 51, 0.6) !important;
        border-color: rgb(255, 87, 51) !important;
        width: 100px;
        text-align: center;
        color: white;
        top: 70px;
        right: 5%;
    }

    .md-chip {
        height: unset !important;
    }

    .info-text {
        font-size: 20px;
        position: relative;
        top: 2px
    }

</style>



