<template>
    <div>
        <div tabindex="0" class="md-dialog-container md-theme-default md-active">
            <div class="md-dialog md-reference cust-height" style="height: 100%;">
                <div class="md-dialog-title md-title">{{words.captions}}</div>
                <div class="md-dialog-content">

                    <md-layout md-gutter>
                        <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100" class="">
                            <md-input-container v-bind:class="[!required_field ? activeClass : 'md-input-invalid']"
                                                style="margin-bottom: 12px">
                                <md-icon>business</md-icon>

                                <label>Network name</label>

                                <md-input v-model="nameClient"></md-input>
                                <span class="md-error">Required.</span>
                                <!--<span class="md-error" v-else>Please enter valid IP address.</span>-->

                            </md-input-container>
                        </md-layout>

                    </md-layout>


                    <h3 style="margin-bottom: 0;">
                        <md-icon class="select-label-icon">
                            people
                        </md-icon>
                        <span class="select-label-text">Users</span>
                    </h3>

                    <div style="margin-bottom: 24px">
                        <v-select ref="select" multiple :options="users" :value.sync="users"
                                  :id="'user_id'" :label="'name'"
                                  :label2="'login'" v-model="selected_obj_users"></v-select>
                    </div>
                    <div v-if="words.updata">
                        <h3 style="margin-bottom: 0;">
                            <md-icon class="select-label-icon">
                                wifi
                            </md-icon>
                            <span class="select-label-text">Nodes</span>
                        </h3>

                        <div v-if="stations">
                            <v-select ref="select" multiple :options="stations" :value.sync="stations"
                                      :id="'id'" :label="'software_version'"
                                      :label2="'ip_address'" v-model="selected_stations"></v-select>
                        </div>
                    </div>
                </div>
                <div class="md-dialog-actions">
                    <md-button class="md-primary" @click="closeDialog()">Cancel</md-button>
                    <md-button class="md-primary" v-if="words.create" :disabled="!(validate && !class_for_time && done) "
                               @click="createClient()">add
                    </md-button>
                    <md-button class="md-primary" v-if="words.updata" :disabled="!(validate && !class_for_time)"
                               @click="updateClient()">edit
                    </md-button>

                </div>
            </div>
            <div class="md-backdrop md-dialog-backdrop md-active"></div>
        </div>


    </div>

</template>

<script>

    import vSelect from './Select.vue'

    export default {

        components: {vSelect},
        name: 'popup-clients',

        data() {
            return {
                done:true,
                items: [],
                users: [],
                stations: [],
                initialValue: '',
                checkbox: true,
                validate: true,
                class_for_time: false,
                required_field: false,
                autocompleteValue: '',
                words: this.$parent.words,
                selected: [],
                selected_obj_users: [],
                selected_id_stations: [],
                selected_stations: [],
                add_selected_id_users: [],
                selected_id_users: [],
                delete_id: [],
                test: "",
                nameClient: '',
                update_time: 0,
                activeClass: '',
                clientObj: {},

            }
        },
        methods: {
            getUsers() {
                this.$parent.$refs.topProgress.start()
                this.$http.get(this.$parent.base_url + 'users',
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.users = response.body;
                        this.$parent.$refs.topProgress.done()
                    }, function (error) {
                        console.log(error.status)
                        this.$parent.error = true;
                        this.$parent.$refs.topProgress.fail()
                        this.$parent.UNAUTHORIZED(error.status)

                    }
                );
            },
            getStations() {
                this.$parent.$refs.topProgress.start()

                this.$http.get(this.$parent.base_url + 'stations/other',
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.stations = response.body.result;
                        this.$parent.$refs.topProgress.done()

                    }, function (error) {
                        console.log(error.status)
                        this.$parent.error = true;
                        this.$parent.$refs.topProgress.fail()
                        this.$parent.UNAUTHORIZED(error.status)

                    }
                );
                self = this
                setTimeout(function () {
                    self.$parent.success = false;
                    self.$parent.error = false;
                }, 1000);
            },


            closeDialog() {
                this.$parent.modalClients = false
            },

            createClient() {
                if (this.nameClient === '') {
                    this.required_field = true;
                    return
                }
                for (let i in this.selected_stations) {
                    this.selected_id_stations.push(this.selected_stations[i].id)

                }
                for (let i in this.selected_obj_users) {
                    this.selected_id_users.push(this.selected_obj_users[i].user_id)

                }
                this.clientObj.stations_id = this.selected_id_stations;
                this.clientObj.users_id = this.selected_id_users;
                this.clientObj.name = this.nameClient;
                this.done=false
                this.$http.post(this.$parent.base_url + 'client', this.clientObj,
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.$parent.event = 'create_station';
                        this.$parent.currentStation = response.body.result;
                        this.$parent.modalClients = false
                        this.$parent.success = true;
                        this.$parent.$refs.topProgress.done()
                        this.done=true

                        self = this
                        setTimeout(function () {
                            self.$parent.success = false;
                            self.$parent.error = false;
                        }, 1000);

                    }, function (error) {
                        this.done=true

                        this.$parent.error = true;
                        this.$parent.modalStations = false
                        this.$parent.$refs.topProgress.fail()
                        self = this
                        setTimeout(function () {
                            self.$parent.success = false;
                            self.$parent.error = false;
                        }, 1000);

                    }
                );


            },
            getClient_byId(id) {
                this.$parent.$refs.topProgress.start()
                this.$http.get(this.$parent.base_url + 'client/' + id,
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.nameClient = response.body.name;
                        this.selected_obj_users = response.body.users
                        this.selected_stations = response.body.stations
                        this.$parent.$refs.topProgress.done()
                        self = this
                        setTimeout(function () {
                            self.$parent.success = false;
                            self.$parent.error = false;
                        }, 1000);

                    }, function (error) {
                        console.log(error.status)
                        this.$parent.error = true;
                        this.$parent.$refs.topProgress.fail()
                        this.$parent.UNAUTHORIZED(error.status)
                        self = this
                        setTimeout(function () {
                            self.$parent.success = false;
                            self.$parent.error = false;
                        }, 1000);

                    }
                );

            },
            updateClient() {
                this.$parent.reloadClients = true;
                if (this.nameClient === '') {
                    this.required_field = true;
                    return
                }
                for (let i in this.selected_stations) {
                    this.selected_id_stations.push(this.selected_stations[i].id)
                }
                for (let i in this.selected_obj_users) {
                    this.selected_id_users.push(this.selected_obj_users[i].user_id)
                }
                this.clientObj.stations_id = this.selected_id_stations;
                this.clientObj.users_id = this.selected_id_users;
                this.clientObj.name = this.nameClient;
                this.$parent.$refs.topProgress.start()

                this.$http.put(this.$parent.base_url + 'client/' + this.words.obj.id, this.clientObj,
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.$parent.event = 'create_station';
                        this.$parent.currentStation = response.body.result;
                        this.$parent.modalClients = false
                        this.$parent.success = true;
                        this.$parent.$refs.topProgress.done()
                        self = this
                        setTimeout(function () {
                            self.$parent.success = false;
                            self.$parent.error = false;
                        }, 1000);

                    }, function (error) {
                        this.$parent.error = true;
                        this.$parent.modalStations = false
                        this.$parent.$refs.topProgress.fail()
                        self = this
                        setTimeout(function () {
                            self.$parent.success = false;
                            self.$parent.error = false;
                        }, 1000);

                    }
                );
            }
        },
        mounted() {
            if (this.words.updata) {
                this.getClient_byId(this.words.obj.id)
            }
            this.getUsers()
            this.getStations()
        },
        created() {
        },
        watch: {

            nameClient: function () {
                this.required_field = !this.nameClient
            },
        }


    }

</script>

<style scoped>

    .md-title {
        font-size: 24px !important;

    }

    h3 {
        /*color:#3F51B9;*/
        margin-bottom: 0 !important;
    }
</style>



