                    </md-table-cell>


                    <md-table-cell v-if="$parent.admin ">

                        <md-button v-if="item.client_id"  @click="EditClients(item.client_id)">
                            <md-icon>edit</md-icon>
                        </md-button>
                    </md-table-cell>
                    <md-table-cell v-if="$parent.admin">
                        <md-button v-if="item.client_id" style="padding-right: 24px" @click="DeleteClients(index,item.client_id)">
                            <md-icon>delete</md-icon>
                        </md-button>
                    </md-table-cell>
                    <md-table-cell style="padding-right: 5px;">
                        <md-button style="padding: 0 10px !important;width: unset  ;  margin-right: 10px;"
                                   v-on:click=" addProperty(index)">
                            <span class="fnt-10">  NODES</span>
                            <md-icon v-if="!item.more">expand_more</md-icon>
                            <md-icon v-if="item.more">expand_less</md-icon>
                        </md-button>
                    </md-table-cell>
                </md-table-row>
                <transition name="drop-down">
                    <md-table-row v-if="item.more " class="box">
                        <md-table-cell colspan="20">
                            <md-table style="margin-bottom: 10px;" class="sub-table">
                                <md-table-header>
                                    <md-table-row class="sub-table-head">
                                        <md-table-head>Node nr.</md-table-head>
                                        <md-table-head>BSC nr.</md-table-head>
                                        <md-table-head>Description </md-table-head>
                                        <md-table-head>Node</md-table-head>
                                        <md-table-head>Common</md-table-head>
                                        <md-table-head>Subscriber</md-table-head>
                                        <md-table-head>Radio </md-table-head>
                                        <md-table-head>VoiceGW </md-table-head>
                                        <md-table-head>PcktGW </md-table-head>
                                        <md-table-head>AppGW </md-table-head>
                                        <md-table-head>TermGW </md-table-head>
                                        <md-table-head>HW type	 </md-table-head>
                                        <md-table-head>Version </md-table-head>
                                        <md-table-head>Active </md-table-head>
                                        <md-table-head></md-table-head>
                                        <md-table-head v-if="$parent.admin"></md-table-head>
                                        <md-table-head v-if="$parent.admin"></md-table-head>
                                    </md-table-row>
                                </md-table-header>
                                <md-table-body v-for="(sub_item, index) in item.stations" :key="index">
                                    <md-table-row>
                                        <md-table-cell>
                                            <div class="sub-table-rows">{{sub_item.node_number}}
                                                <span v-if="!sub_item.node_number">—</span>
