                                        </md-table-cell>
                                        <md-table-cell>
                                            <div class=" round" :class="sub_item.subscriber_alarm">
                                                <span v-if="!sub_item.subscriber_alarm"> —</span>

                                            </div>
                                        </md-table-cell>
                                        <md-table-cell>
                                            <div class=" round" :class="sub_item.radio">
                                                <span v-if="!sub_item.radio"> —</span>
                                            </div>
                                        </md-table-cell>
                                        <md-table-cell>
                                            <div class=" round" :class="sub_item.voice">
                                                <span v-if="!sub_item.voice"> —</span>
                                            </div>
                                        </md-table-cell>
                                        <md-table-cell>
                                            <div class=" round" :class="sub_item.pckt">
                                                <span v-if="!sub_item.pckt"> —</span>
                                            </div>
                                        </md-table-cell>
                                        <md-table-cell>
                                            <div class=" round" :class="sub_item.app">
                                                <span v-if="!sub_item.app"> —</span>
                                            </div>
                                        </md-table-cell>
                                        <md-table-cell>
                                            <div class="round" :class="sub_item.term">
                                                <span v-if="!sub_item.term"> —</span>
                                            </div>
                                        </md-table-cell>
                                        <md-table-cell>
                                            <div class="sub-table-rows">{{sub_item.hardware_type}}
                                                <span v-if="!sub_item.hardware_type">—</span>
                                            </div>
                                        </md-table-cell>
                                        <md-table-cell>
                                            <div class="sub-table-rows">{{sub_item.software_version}}
                                                <span v-if="!sub_item.software_version">—</span>
                                            </div>
                                        </md-table-cell>
                                        <md-table-cell>

                                            <div class=" round green" v-if="sub_item.status" >
                                            </div>
                                            <span v-if="!sub_item.status">—</span>

                                        </md-table-cell>
                                        <md-table-cell style="padding-right: 12px;">

                                            <md-button @click="$parent.openLogs(sub_item.id)">
                                                <md-icon>description</md-icon>
                                            </md-button>
                                        </md-table-cell>
                                        <md-table-cell v-if="$parent.admin">
                                            <md-button @click="EditStations(sub_item.id)">
                                                <md-icon>edit</md-icon>
                                            </md-button>
                                        </md-table-cell>
                                        <md-table-cell v-if="$parent.admin" style="padding-right: 24px;">
                                            <md-button style="padding-right: 24px"
                                                       @click="DeleteStations(index,sub_item.id)">
                                                <md-icon>delete</md-icon>
                                            </md-button>
