                                    </md-table-row>

                                </md-table-body>
                            </md-table>
                        </md-table-cell>
                    </md-table-row>
                </transition>
                </tbody>

            </md-table>
        </div>
        <div v-if="empty && $parent.admin"> List of stations is empty, <span class="empty-list"
                                                                             @click="$parent.CreateStations()">click this and create one </span>
        </div>
        <div v-if="empty && !$parent.admin">
            You was not assigned to any Base Station yet. Try again later or contact administrator.
        </div>
    </div>

</template>

<script>
    import PopupStations from "./popup-stations.vue";
    import router from '.././router'

    export default {

        components: {
            PopupStations
        },
        name: 'stations',
        data() {
            return {
                stations: [],
                clients: [],
                step: 0,
                progress: 0,
                progressInterval: null,
                done: false,
                transition: true,
                activeClass: '',
                opens_index: [],
                opens_index_padding: [],
                current_index: null,
                success: this.$parent.success,
                empty: false,
                showAll: true,
                preReload: true,
                start: false,

            }
        },
        router,
        methods: {
            startProgressLine() {
                if (this.$parent.interval) {
                    clearInterval(this.clearInterval)
                    this.clearInterval = window.setTimeout(() => {
                        if (this.$refs.topProgress_table) {
                            this.$refs.topProgress_table.start();

                            this.step += 1;
                            this.$refs.topProgress_table.increase(1)
                            this.$refs.topProgress_table.pause()
                        }
                        if (this.step == 95) {
                            this.$refs.topProgress_table.done();
                            this.getClients()
                            this.step = 0
                            this.start = false
                        }
                        this.startProgressLine()
                    }, 300);
                }
            },

            allStationsHideShow() {
                if (this.showAll) {
                    for (let i in this.clients) {
                        this.clients[i]['more'] = true
                        this.opens_index.push(+i)
                    }
                } else {
                    for (let i in this.clients) {
                        this.clients[i]['more'] = false
                        this.opens_index = []
                    }
                }
                this.showAll = !this.showAll
            },
            addProperty(index) {

                this.clients[index]['more'] = !this.clients[index]['more']
                if (this.clients[index]['more']) {
                    this.opens_index.push(index)
                } else {
                    this.opens_index.splice(this.opens_index.indexOf(index), 1);
                }

            }
            ,
            ParseColor(index, value) {

                if (value === 'hardware_type') {
                    if (this.stations[index][value] === 0) {
                        this.stations[index][value] = 'PC';

                    }
                    else if (this.stations[index][value] === 1) {
                        this.stations[index][value] = 'BSC421';
                    } else if (this.stations[index][value] === 2) {
                        this.stations[index][value] = 'BSC412';
                    }

                }
                else {
                    if (this.stations[index][value] === 0) {
                        this.stations[index][value] = 'green';

                    }
                    else if (this.stations[index][value] === 1) {
                        this.stations[index][value] = 'yellow';
                    } else if (this.stations[index][value] === 2) {
                        this.stations[index][value] = 'red';
                    } else if (this.stations[index][value] === 3) {
                        this.stations[index][value] = 'purple';
                    } else {
                        let content = value + '_content';
                        this.stations[index][value] = '';
                        this.stations[index][content] = '—';
                    }
                }
            },
            getClients() {
                this.$parent.interval = true;
                this.$parent.$refs.topProgress.start()
                this.$http.get(this.$parent.base_url + 'client/stations',
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.clients = response.body.clients
                        for (let index in this.opens_index) {
                            if (this.clients[this.opens_index[index]]) {
                                this.clients[this.opens_index[index]]['more'] = true
                            }
                        }
                        this.empty = !this.clients.length;
                        this.$parent.$refs.topProgress.done()
                        this.start = true;
                        this.startProgressLine();
                    }, function (error) {
                        console.log(error.status)
                        this.$parent.error = true;
                        this.$parent.$refs.topProgress.fail()
                        this.$parent.UNAUTHORIZED(error.status)

                    }
                );
                self = this
                setTimeout(function () {
                    self.$parent.success = false;
                    self.$parent.error = false;
                }, 1000);
            }
            ,
            EditStations(id) {
                this.$parent.modalUsers = false
                this.$parent.modalStations = true
                this.$parent.words = {
                    'captions': 'Edit node',
                    create_btn: false,
                    updata_btn: true,
                    obj: {
                        id: id
                    }
                }
            }
            ,
            EditClients(id) {
                this.$parent.modalUsers = false
                this.$parent.modalStations = false
                this.$parent.modalClients = true
                this.$parent.words = {
                    'captions': 'Edit  network',
                    create: false,
                    updata: true,
                    obj: {
                        id: id
                    }
                }
            }
            ,

            DeleteStations(index, id) {
                this.current_index = index;

                this.$parent.words_delete = {
                    'captions': 'node',
                    station: true,
                    user: false,
                    client: false,
                    obj: {
                        current_index: index,
                        id: id
                    }

                }
                this.$parent.modalDelete = true

            }
            ,
            DeleteClients(index, id) {
                this.current_index = index;

                this.$parent.words_delete = {
                    'captions': 'network',
                    station: false,
                    user: false,
                    client: true,
                    obj: {
                        current_index: index,
                        id: id
                    }

                }
                this.$parent.modalDelete = true

            },
        },
        mounted() {

            this.getClients()

        },
        computed: {

            interval() {
                return this.$parent.interval
            },
            reload() {
                return this.$parent.reloadClients;
            },
            event() {
                return this.$parent.event;
            },
            filteredStations() {
                return this.stations;
            },
            interval() {
                return this.$parent.interval
            },

        },
        watch: {
            interval: function () {
            },
            reload: function () {
                if (this.reload) {
                    this.getClients();
                    this.$parent.reloadClients = false;
                }
            },
            event: function () {
                if (this.event === 'delete') {
                    this.getClients();
                    this.$parent.event = '';
                }
                if (this.event === 'delete_client') {
                    this.getClients();
                    this.$parent.event = '';
                }
                if (this.event === 'create_client') {
                    this.getClients();

                    this.$parent.event = '';
                }
                if (this.event === 'update_stations') {
                    this.getClients();

                    this.$parent.event = '';
                }
                if (this.event === 'create_station') {
                    this.getClients();
                    this.$parent.currentStation = {};
                    this.$parent.event = '';

                }
            },
        },
        created() {
            this.$parent.interval = true
            if (this.$parent.showNotifications) {
                this.$parent.modalNotifications = true;
                this.$parent.showNotifications = false;
            }
            this.$parent.title = 'Tetra networks'

        }


    }
</script>

<style scoped>
    #topProgress_table {
        position: static !important;
    }
    .top-progress {
        position: relative !important;
        z-index: 99 !important;
        top: 100% !important;
    }
    .peg {
        height: 0 !important;
        width: 0 !important;
        box-shadow: rgba(28, 255, 0, 0) !important;
        position: relative !important;
    }

    .hover-tooltip {
        padding-top: 10px;
    }
    .xbtooltip {
        display: none;
        position: absolute;
        background-color: #3f51b9;
        color: #fff;
        padding: 5px;
        border-radius: 5px;
    }
    .castom-tb-body {
        border-bottom: 10px solid #ebeff2 !important;
        border-top: 10px solid #ebeff2 !important;
    }
    .btn-show-hide {
        background: rgba(0, 0, 0, 0.109);;
        padding: 7px;
        border-radius: 3px;
    }
    .btn-show-hide:hover {
        background: rgba(0, 0, 0, 0.209);;

    }
    .fnt-10 {
        font-size: 10px;
    }
    .show-all:hover {
        cursor: pointer;
    }
    .sub-table {
        overflow-x: hidden !important;
        margin: 0 auto !important;
    }

    .sub-table-head {
        padding: 0 10px !important;
    }

    .sub-table-rows {
        margin: auto;
    }

    .empty-list {
        color: #3f51b9;
    }

    .empty-list:hover {
        cursor: pointer;
    }

    .card-custom {
        width: 265px;
        margin-top: 40px;
        margin-bottom: 20px
    }

</style>



