import Vue from 'vue'
import Router from 'vue-router'
import Stations from '@/components/stations'
import Users from '@/components/users'
import Login from '@/components/login'
import Change_password from '@/components/change-password'
import Notifications from '@/components/notifications'

import VueCookies from 'vue-cookies'

Vue.use(VueCookies)

Vue.use(Router)

export default new Router({
    // mode: 'history',

    routes: [

        {
            path: '/',
            name: 'home',
            component: Stations,
            beforeEnter: (to, from, next) => {

                if (!VueCookies.get("token") || VueCookies.get("token") === 'null' || VueCookies.get("token") === '') {
                    next('login');
                } else {
                    next()
                }
            }
        }, {
            path: '/stations',
            name: 'stations',
            component: Stations,
            beforeEnter: (to, from, next) => {
                if (!VueCookies.get("token") || VueCookies.get("token") === 'null' || VueCookies.get("token") === '') {
                    next('login');
                } else {
                    next()
                }
            }


        }, {
            path: '/users',
            name: 'users',
            component: Users,

            beforeEnter: (to, from, next) => {
                console.log(VueCookies.get("admin"))
                if (!VueCookies.get("token") || VueCookies.get("token") === 'null' || VueCookies.get("token") === '' || VueCookies.get("admin") != 'admin') {
                    next('login');
                } else {
                    next()
                }
            }

        },{
            path: '/notifications',
            name: 'notifications',
            component: Notifications,

            beforeEnter: (to, from, next) => {
                console.log(VueCookies.get("admin"))
                if (!VueCookies.get("token") || VueCookies.get("token") === 'null' || VueCookies.get("token") === '' || VueCookies.get("admin") != 'admin') {
                    next('login?notifications=true');

                } else {
                    console.log(this)
                    next()
                }
            }

        }, {
            path: '/login',
            name: 'login',
            component: Login,
            beforeEnter: (to, from, next) => {

                if (VueCookies.get("token") && VueCookies.get("token") !== null && VueCookies.get("token") !== 'null' && VueCookies.get("token") !== '') {
                    next('/');
                } else {
                    next()
                }
            }},

        // }, {
        //     path: '/password/:token',
        //     name: 'change_password',
        //     component: Change_password,
        //     // beforeEnter: (to, from, next) => {
        //     //     if (VueCookies.get("token") && VueCookies.get("token") !== null &&  VueCookies.get("token") !== 'null' && VueCookies.get("token") !== '') {
        //     //     next('/');
        //     //   }else {
        //     //     next()
        //     //   }
        //     // }
        //
        // },
        {path: '/password/change', component: Change_password, props: (route) => ({query: route.query.token})},
        {
            path:'*',
            redirect:"/"
        }


    ]
})



