<template>
    <div class="login_page">


        <div class="container">

            <form @submit.prevent="Log_in()" class="form_login">
                <div style="text-align: center">
                    <img src="../assets/logo2.svg" class="logo-image">
                </div>

                <md-layout md-gutter>
                    <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100" md-flex-large="100"
                               md-flex-xlarge="100" class="pad">

                        <md-input-container v-bind:class="[validate_login ? activeClass : 'md-input-invalid']">
                            <md-icon>mail</md-icon>

                            <label>Email</label>
                            <md-input type="email" v-model="login"></md-input>
                            <span class="md-error" v-if="required_field">Required.</span>
                            <span class="md-error" v-else="!error_message">Please enter valid email address.</span>
                        </md-input-container>
                    </md-layout>
                    <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100" md-flex-large="100"
                               md-flex-xlarge="100" class="pad">
                        <md-input-container v-bind:class="[!required_pass ? activeClass : 'md-input-invalid']"
                                            md-has-password>
                            <md-icon>lock</md-icon>

                            <label>Password</label>
                            <md-input type="password" v-model="password"></md-input>
                            <span class="md-error">Required.</span>

                        </md-input-container>
                        <span class="md-error" style="color: red" v-if="error_login">{{error_login}}</span>

                    </md-layout>

                </md-layout>

                <md-button type="submit" class="md-raised md-accent" :disabled="!validate_login && !required_pass">
                    sign in
                </md-button>

            </form>
        </div>

    </div>
</template>

<script>
    import router from '.././router'

    export default {
        components: {},
        name: 'Login',
        data() {
            return {
                validate_login: true,
                required_field: false,
                required_pass: false,
                login_form: {},
                login: '',
                error_login: '',
                password: '',
                activeClass: '',
            }
        },
        router,
        methods: {
            Log_in() {
                if (this.login === '') {
                    this.validate_login = false;
                    this.required_field = true;
                    return
                }
                if (this.password === '') {
                    this.required_pass = true;
                    return
                }
                this.$parent.$refs.topProgress.start()
                this.login_form.login = this.login
                this.login_form.password = this.password
                this.$http.post(this.$parent.base_url + 'login', this.login_form).then(
                    function (response) {
                        this.error_login = ''
                        this.$cookies.set("token", response.data.token);
                        this.$cookies.set("admin", response.data.admin ? "admin" : '');
                        this.$parent.nav_bar = true;
                        this.$parent.admin = response.data.admin ? "admin" : '';
                        router.push('/stations');
                        this.$parent.$refs.topProgress.done()
                    }, function (error) {
                        this.error_login = error.body.message
                        console.log("error")
                        this.$parent.$refs.topProgress.fail()
                    }
                );
                self = this
                setTimeout(function () {
                    self.$parent.success = false;
                    self.$parent.error = false;
                }, 1000);
            },
        }, watch: {

            login: function () {
                this.error_login = ''

                if (this.login) {
                    this.required_field = false
                    let re = new RegExp('^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,4})$')
                    this.validate_login = !!this.login.match(re);
                } else {
                    this.required_field = true

                    this.validate_login = false
                }

            }, password: function () {
                this.error_login = ''

                if (this.password) {
                    this.required_pass = false

                } else {
                    this.required_pass = true

//                    this.validate_login = false
                }

            },


        },

        created() {
            this.$parent.interval=false
            if (this.$route.query.notifications) {
                this.$parent.showNotifications = true
            }
            this.$parent.title = 'Login'
        }


    }

</script>

<style scoped>
    .logo-image {
        width: 220px;
        height: 66px;
        margin-bottom: 15px;
    }

</style>



