<template>
    <div>


        <div tabindex="0" class="md-dialog-container md-theme-default md-active">
            <div class="md-dialog md-reference"  style="height: 100%;">
                <div class="md-dialog-title md-title">{{words.captions}}</div>
                <div class="md-dialog-content">


                    <md-layout md-gutter>

                        <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100">
                            <md-input-container v-bind:class="[validate_login  ? activeClass : 'md-input-invalid'] ">
                                <md-icon>mail</md-icon>

                                <label>Email</label>
                                <md-input v-model="login"></md-input>
                                <span class="md-error" v-if="error_message">{{error_message}}</span>
                                <div v-else>
                                    <span class="md-error" v-if="required_field">Required.</span>
                                    <span class="md-error"
                                          v-else="!error_message">Please enter valid email address.</span>
                                </div>
                            </md-input-container>
                        </md-layout>


                    </md-layout>
                    <md-layout md-gutter>
                        <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100">
                            <md-input-container v-bind:class="[validate_phone ? activeClass : 'md-input-invalid']">
                                <md-icon>phone</md-icon>

                                <label>Phone</label>
                                <md-input v-model="phone" type="text"></md-input>
                                <span class="md-error">Please enter valid phone number.</span>

                            </md-input-container>
                        </md-layout>


                    </md-layout>

                    <md-layout md-gutter>
                        <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100">
                            <md-input-container>
                                <md-icon>face</md-icon>


                                <label>Name</label>
                                <md-input v-model="userObj.name"></md-input>
                            </md-input-container>
                        </md-layout>


                    </md-layout>
                    <md-layout md-gutter>

                        <md-layout md-gutter style="margin-top: 14px">
                            <md-layout md-flex-xsmall="100" md-flex-small="100" md-flex-medium="100">


                                <div class="custom-switch" v-bind:class="{ 'custom-active':!isAdmin }">
                                    User
                                </div>
                                <div class="custom-switch">
                                    <md-switch v-model="isAdmin" class="md-primary">
                                    </md-switch>
                                </div>
                                <div class="custom-switch" v-bind:class="{ 'custom-active': isAdmin }">
                                    Admin
                                </div>
                            </md-layout>


                        </md-layout>


                    </md-layout>
                    <h3 style="margin-bottom: 0;">
                        <md-icon class="select-label-icon">
                            business
                        </md-icon>
                        <span class="select-label-text" v-bind:class="[!isAdmin ? activeClass : 'disabled-text']">Add to networks</span>
                    </h3>

                    <div>
                        <v-select ref="select" multiple :options="clients" :value.sync="clients"
                                  :id="'id'" :label="'name'" :disabled="isAdmin"
                                  :label2="'name'" v-model="selected_clients"></v-select>

                    </div>
                </div>
                <div class="md-dialog-actions">
                    <md-button class="md-primary" @click="closeDialog('dialog2')">Cancel</md-button>
                    <md-button class="md-primary" v-if="words.create" @click="createUser('dialog2')"
                               :disabled="!(validate_phone && validate_login && done )">Add
                    </md-button>
                    <md-button class="md-primary" v-if="words.update" @click="updateUser('dialog2')"
                               :disabled="!(validate_phone && validate_login && done) ">Edit
                    </md-button>

                </div>
            </div>
            <div class="md-backdrop md-dialog-backdrop md-active"></div>
        </div>


    </div>

</template>

<script>
    import vSelect from './Select.vue'

    export default {
        components: {vSelect},

        name: 'popup-users',

        data() {
            return {
                done:true,
                initialValue: 'My initial value',
                checkbox: true,
                login: '',
                phone: '',
                activeClass: '',
                isAdmin: false,
                validate_phone: true,
                validate_login: true,
                required_field: false,
                error_message: '',
                clients: [],
                userObj: {},
                userObjOld: {},
                clients_id: [],
                selected_clients: [],
                autocompleteValue: '',
                words: this.$parent.words

            }
        },
        methods: {

            getClients() {
                this.$parent.$refs.topProgress.start()
                this.$http.get(this.$parent.base_url + 'clients',
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.clients = response.body;

                        this.$parent.$refs.topProgress.done()

                    }, function (error) {
                        console.log(error.status)
                        this.$parent.error = true;
                        this.$parent.$refs.topProgress.fail()
                        this.$parent.UNAUTHORIZED(error.status)

                    }
                );
            },
            closeDialog(ref) {
                if (this.words.update) {
                    this.$parent.event = 'update_user';

                }
                this.$parent.modalUsers = false
            },
            createUser() {

                if (this.login === '') {
                    this.validate_login = false;
                    this.required_field = true;
                    return
                }
                this.$parent.$refs.topProgress.start()

                if (this.isAdmin) {
                    this.userObj.role = 'admin'
                } else {
                    this.userObj.role = 'user'

                }
                for (let i in this.selected_clients) {
                    this.clients_id.push(this.selected_clients[i].id)

                }
                this.userObj.clients_id = this.clients_id;
                this.userObj.login = this.login;
                this.userObj.phone = this.phone;
                this.done=false
                this.$http.post(this.$parent.base_url + 'user', this.userObj,
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.done=true

                        this.$parent.event = 'create_user';
                        this.$parent.currentUser = response.body.result;
                        this.$parent.modalUsers = false
                        this.$parent.success = true;
                        this.$parent.$refs.topProgress.done()
                        this.error_message = '';
                        self = this
                        setTimeout(function () {
                            self.$parent.success = false;
                            self.$parent.error = false;
                        }, 1000);
                    }, function (error) {
                        this.done=true

                        console.log(error)
                        this.$parent.error = true;
                        this.validate_login = false;
//                        this.$parent.modalUsers = false
                        this.$parent.$refs.topProgress.fail()
                        this.error_message = error.body.message;
                        self = this
                        setTimeout(function () {
                            self.$parent.success = false;
                            self.$parent.error = false;
                        }, 1000);
                    }
                );

            },
            updateUser() {
                if (this.login === '') {
                    this.validate_login = false;
                    this.required_field = true;
                    return
                }
                this.$parent.$refs.topProgress.start()

                if (this.isAdmin) {
                    this.userObj.role = 'admin'
                } else {
                    this.userObj.role = 'user'

                }
                for (let i in this.selected_clients) {
                    this.clients_id.push(this.selected_clients[i].id)

                }
                this.userObj.clients_id = this.clients_id;
                this.userObj.login = this.login;
                this.userObj.phone = this.phone;
                let temp = JSON.parse(JSON.stringify(this.words.obj))
                this.done=false

                this.$http.put(this.$parent.base_url + 'user/' + this.words.id, this.userObj,
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.done=true

                        this.$parent.event = 'update_user';

                        this.$parent.currentUser = response.body.result;
                        this.$parent.modalUsers = false
                        this.$parent.success = true;
                        this.$parent.$refs.topProgress.done()

                    }, function (error) {
                        this.done=true

                        this.$parent.error = true;
                        this.validate_login = false;
//                        this.$parent.modalUsers = false
                        this.$parent.$refs.topProgress.fail()
                        this.error_message = error.body.message;
//                        this.$parent.event = 'error_update_user';
                        this.$parent.words.obj = this.userObjOld
                        this.$parent.error = true;
                        this.$parent.$refs.topProgress.fail()


                    }
                );
                self = this
                setTimeout(function () {
                    self.$parent.success = false;
                    self.$parent.error = false;
                }, 1000);
            },

        },
        mounted() {
            this.getClients()
        },
        created() {
            if (this.words.update) {
                this.userObj = this.words.obj
                this.login = this.words.obj.login;
                this.phone = this.words.obj.phone;
                if (this.words.obj.clients) {
                    this.selected_clients = this.words.obj.clients;
                } else {
                    this.selected_clients = []

                }
                this.userObjOld = JSON.parse(JSON.stringify(this.words.obj))
                this.isAdmin = this.words.obj.role === 'admin'

            }
        },
        watch: {

            isAdmin: function () {
                if (this.isAdmin) {
                    this.selected_clients = []
                }
            },
            login: function () {
                this.error_message = ''
                if (this.login) {
                    this.required_field = false
                    let re = new RegExp('^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,4})$')
                    this.validate_login = !!this.login.match(re);
                } else {
                    this.required_field = true

                    this.validate_login = false
                }

            }, phone: function () {
                if (this.phone) {
                    let re2 = new RegExp('^[\+][0-9]{9,15}$', 'g')
                    this.validate_phone = !!this.phone.match(re2);

                } else {
                    this.validate_phone = true
                }

            },
        }


    }

</script>

<style>

    .md-switch {
        margin: 0 !important;
        margin-top: 4px !important;
        /*display: -ms-inline-flexbox;*/
        /*display: inline-flex;*/
        /*position: relative;*/
    }

    .custom-switch {
        margin: 5px !important;
    }

    .custom-active {
        color: #3f51b9;
    }

</style>



