<template>
    <div>
        <div tabindex="0" class="md-dialog-container md-theme-default md-active">
            <div class="md-dialog md-reference" style="height: 100%;">
                <div class="md-dialog-title md-title">Notifications</div>
                <div class="md-dialog-content">
                    <div class="flex-container">
                        <div></div>
                        <div>Notifications</div>
                        <div>Report</div>
                    </div>
                    <div class="flex-container" v-for="item in notifications">
                        <div>{{item.name}}</div>
                        <div>
                            <span @click="item.email_alarm = !item.email_alarm" class="notification"
                                   >
                                 <!--<md-icon tabindex="1" v-if="item.notification" class="notification-active button">notifications_active</md-icon>-->
                                <!--<md-icon tabindex="1" v-if="!item.notification" class="notification-passive button">notifications</md-icon>-->
                           <md-icon tabindex="1" v-if="item.email_alarm"
                                    class="notification-active button">mail</md-icon>
                                 <md-icon tabindex="1" v-if="!item.email_alarm" class="notification-passive button">mail_outline</md-icon>
                            </span>
                            <!--<md-switch v-model="item.email_alarm" class="md-primary">-->
                            <!--</md-switch>-->
                        </div>
                        <div>

                            <md-input-container>
                                <md-select name="report" id="report" v-model="item.report">
                                    <md-option value="never">Never</md-option>
                                    <md-option value="weekly">Weekly</md-option>
                                    <md-option value="daily">Daily</md-option>
                                    <md-option value="monthly">Monthly</md-option>

                                </md-select>
                            </md-input-container>
                        </div>
                    </div>
                </div>
                <div class="md-dialog-actions">
                    <md-button class="md-primary" @click="closeDialog()">Cancel</md-button>
                    <md-button class="md-primary" @click="doneNotifacation()">Save</md-button>

                </div>
            </div>
            <div class="md-backdrop md-dialog-backdrop md-active"></div>

        </div>
    </div>
</template>

<script>
    import router from '.././router'

    export default {
        components: {},
        name: 'notifications',
        data() {
            return {
                validate_confirm_password: false,
                required_field: false,
                required_pass: false,
                no_validate_tokint: false,
                isAdmin: false,
                password_form: {},
                notifications: {},
                data_cust: {},
                sendNotifications: {},
                token: '',
                login: '',
                error_login: '',
                password: '',
                confirm_password: '',
                activeClass: '',
            }
        },
        router,
        methods: {
            closeDialog() {
                this.$parent.modalNotifications = false
                router.push('/');

            },
            getNotifacation() {
                this.$parent.$refs.topProgress.start()

                this.$http.get(this.$parent.base_url + 'notification',
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.$parent.$refs.topProgress.done()
                        this.notifications= response.body.result
                    }, function (error) {
                        console.log(error.status)
                        this.$parent.error = true;
                        this.$parent.$refs.topProgress.fail()
                        this.$parent.UNAUTHORIZED(error.status)

                    }
                );
                self = this
                setTimeout(function () {
                    self.$parent.success = false;
                    self.$parent.error = false;
                }, 1000);
            },
            doneNotifacation() {
                this.$http.put(this.$parent.base_url + 'notification', this.notifications,
                    {headers: {'Authorization': 'Bearer ' + this.$cookies.get("token")}}).then(
                    function (response) {
                        this.$parent.$refs.topProgress.done()
                        this.$parent.modalNotifications = false
                        self.$parent.success = true;
                    }, function (error) {
                        console.log("error")
                        this.$parent.modalNotifications = false
                        self.$parent.error = true;

                        this.$parent.$refs.topProgress.fail()
                    }
                );
                self = this
                setTimeout(function () {
                    self.$parent.success = false;
                    self.$parent.error = false;
                }, 1000);
            },
        },
        watch: {},
        mounted(){
        },
        created() {
            this.getNotifacation()
        }


    }

</script>

<style scoped>

    .md-button {
        padding: 0 10px !important;
    }

    .md-select {
        min-width: unset !important;
    }

    .md-dialog {
        margin-bottom: 10vh !important;
        width: unset !important;
        height: unset !important;
    }

    .notification-passive {
        color: #0000008a;
    }

    .notification-passive {
        color: #0000008a;
    }

    .notification-active {
        color: #3F51B9;

    }

    .notification {
        cursor: pointer;
    }

    .flex-container {
        display: flex;
        align-items: center;
    }

    .flex-container > div {
        /*background-color: #f1f1f1;*/
        width: 100px;
        margin: 10px;
        text-align: center;
        /*line-height: 75px;*/
        /*font-size: 30px;*/
    }

    .custom-select {
        margin: 0;
        margin-left: 5px;
        line-height: unset;
        float: left;
    }

    .button {
        position: relative;
        display: inline-block;
        cursor: pointer;

        -webkit-transition: all 0.4s ease-in;
        transition: all 0.4s ease-in;
    }

    .button:before {
        content: '';
        background-color: #3F51B9;
        border-radius: 50%;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        -webkit-transform: scale(0.001, 0.001);
        transform: scale(0.001, 0.001);
    }

    .button:focus {
        outline: 0;
    }

    .button:focus:before {
        -webkit-animation: effect_dylan 0.8s ease-out;
        animation: effect_dylan 0.8s ease-out;
    }

    @-webkit-keyframes effect_dylan {
        50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
        }
        99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
        }
        100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
        }
    }

    @keyframes effect_dylan {
        50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
        }
        99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
        }
        100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
        }
    }


</style>



